import { CHANGE_TARIFF, CHANGE_VALUE } from './types'
export const changeTariff=({tariff,radioValue})=>dispatch=>{
  dispatch({
    type:CHANGE_TARIFF,
    payload:{
      tariff,
      radioValue
    }
  })
}
export const changeValue=({checkBoxValue,staticIP})=>dispatch=>{
  dispatch({
    type:CHANGE_VALUE,
    payload:{
      checkBoxValue,
      staticIP
    }
  })
}
