import { CHANGE_TARIFF, CHANGE_VALUE } from '../actions/types'

const initState={
  currency:'rub/mo',
  tariff:[[1000,'Tariff S'],[2000,'Tariff M'],[3000,'Tariff L']],
  radioValue:1000,
  checkBoxValue:0,
  tariffInit:'Tariff S',
  staticIP:false
}
export default function counter(state = initState, action) {
  switch (action.type) {
    case CHANGE_TARIFF:
      return {...state,radioValue:action.payload.radioValue,tariffInit:action.payload.tariff}
    case CHANGE_VALUE:
      return {...state,checkBoxValue:action.payload.checkBoxValue,staticIP:action.payload.staticIP}
    default:
      return state
  }
}