import React from 'react'
import { connect } from 'react-redux'
import { changeTariff, changeValue } from '../actions/tariffActions'

class InternetCalc extends React.Component {

  handleOnChange = (e) => {
    let tariff = e.target.value.split(',')[1]
    let radioValue = e.target.value.split(',')[0]
    const { changeTariff } = this.props
    changeTariff({ tariff, radioValue })
  }
  handleClick = (e) => {
    let staticIP = e.target.checked
    const { changeValue } = this.props
    if (staticIP) {
      changeValue({ checkBoxValue: e.target.value, staticIP })
    } else {
      changeValue({ checkBoxValue: 0, staticIP })
    }
  }

  render() {
    const {
      tariff,
      currency,
      radioValue,
      checkBoxValue,
      tariffInit,
      staticIP,
    } = this.props.tariff
    return (
      <>
        <div className='container '>
          <h4>Created by Automedon</h4>
          <div className="row">

            {/*Left*/}

            <div className="col-12 col-lg-8 col-md-6  col-sm-12 col-xs-12 border rounded bg-white">
              <div className='py-4 px-4 '>
                <table className="table table-bordered mb-0 ">
                  <thead className="table table-dark">
                  <tr>
                    <th scope="col" className='border-right-0'>Tariff</th>
                    <th scope="col" className='border-left-0'/>
                  </tr>
                  </thead>
                  <tbody>
                  <tr className={`${tariffInit !== tariff[0][1] ? 'text-secondary' : null} bg-white`}>
                    <th scope="row">
                      <div className="custom-control custom-radio border-left-0">
                        <input className="custom-control-input" type="radio" name="exampleRadios" id="exampleRadios1"
                               value={tariff[0]} onChange={this.handleOnChange} defaultChecked/>
                        <label className="custom-control-label" htmlFor="exampleRadios1">
                          {tariff[0][1]}
                        </label>
                      </div>
                    </th>
                    <td className='text-center'>{`${tariff[0][0]} ${currency}`}</td>
                  </tr>
                  <tr className={`${tariffInit !== tariff[1][1] ? 'text-secondary' : null} bg-white`}>
                    <th scope="row">
                      <div className="custom-control custom-radio">
                        <input className="custom-control-input" type="radio" name="exampleRadios" id="exampleRadios2"
                               value={tariff[1]} onChange={this.handleOnChange}/>
                        <label className="custom-control-label" htmlFor="exampleRadios2">
                          {tariff[1][1]}
                        </label>
                      </div>
                    </th>
                    <td className='text-center'>{`${tariff[1][0]} ${currency}`}</td>
                  </tr>
                  <tr className={`${tariffInit !== tariff[2][1] ? 'text-secondary' : null} bg-white`}>
                    <th scope="row">
                      <div className="custom-control custom-radio">
                        <input className="custom-control-input" type="radio" name="exampleRadios" id="exampleRadios3"
                               value={tariff[2]} onChange={this.handleOnChange}/>
                        <label className="custom-control-label" htmlFor="exampleRadios3">
                          {`${tariff[2][1]} (Static IP included)`}
                        </label>
                      </div>
                    </th>
                    <td className='text-center'>{`${tariff[2][0]} ${currency}`}</td>
                  </tr>
                  {tariffInit !== `${tariff[2][1]}` ? <>
                    <tr>
                      <th scope="col" className='border-right-0 table-dark'>Additional options</th>
                      <th scope="col" className='border-left-0 table-dark'/>
                    </tr>
                    <tr className={`${!staticIP ? 'text-secondary' : null} bg-white`}>
                      <th scope="row">
                        <div className="custom-control custom-checkbox">
                          <input className="custom-control-input" type="checkbox" name="check" id="exampleRadios4"
                                 value='145.5' onClick={this.handleClick} defaultChecked={staticIP}/>
                          <label className="custom-control-label" htmlFor="exampleRadios4">
                            Static IP activation
                          </label>
                        </div>
                      </th>
                      <td className='text-center'>145.5 {`${currency}`}</td>
                    </tr>
                    <tr className='bg-white'>
                      <th scope="row">
                        <div className="">
                          Payment for static IP
                        </div>
                      </th>
                      <td className='text-center'>92 {`${currency}`}</td>
                    </tr>
                  </> : null}
                  </tbody>
                </table>
              </div>
            </div>

            {/*Right*/}

            <div className="col-12 col-lg-4 col-md-6 col-sm-12 col-xs-12 container-fluid">
              <div className='border bg-white rounded-top'>
                <div className='pt-4 px-1 '>

                  <table className="table table-sm my-0 table-borderless ">
                    <thead>
                    <tr>
                      <th scope="col" style={{ width: '70%' }}><strong>Internet for business</strong></th>
                      <th scope="col" style={{ width: '30%' }}/>
                    </tr>
                    </thead>
                    <tbody>
                    <tr>
                      <th scope="row" className='text-left text-secondary'>{tariffInit}</th>
                      <td className='text-right'>{`${radioValue} ${currency}`}</td>
                    </tr>
                    {tariffInit !== 'Tariff L' ? <>
                      <tr>
                        <th scope="row" className='text-left text-secondary'>Static IP activation</th>
                        <td className='text-right'>{`${checkBoxValue} ${currency.split('/')[0]}`}</td>
                      </tr>
                      <tr>
                        <th scope="row" className='text-left text-secondary'>Static IP</th>
                        <td className='text-right'>{`${92} ${currency}`}</td>
                      </tr>
                    </> : null}
                    </tbody>
                  </table>
                </div>
              </div>
              <div className='border  rounded-bottom'>
                <div className='px-1'>

                  <table className="table table-sm table-borderless">
                    <thead>
                    <tr>
                      <th scope="col" style={{ width: '70%' }}><strong>Total price</strong></th>
                      <th scope="col" style={{ width: '30%' }}/>
                    </tr>
                    </thead>
                    <tbody>
                    <tr>
                      <th scope="row" className='text-left'>Static IP activation</th>
                      <td
                        className='text-right'>{`${checkBoxValue && tariffInit !== 'Tariff L' ? 145.5 : 0} ${currency.split('/')[0]}`}</td>
                    </tr>
                    <tr>
                      <th scope="row" className='text-left'>Monthly payment</th>
                      <td
                        className='text-right'>{`${radioValue * 1 + (tariffInit === 'Tariff L' ? 0 : 92)} ${currency.split('/')[0]}`}</td>
                    </tr>
                    <tr>
                      <th scope="row" className='text-left'>Yearly payment</th>
                      <td
                        className='text-right'>{`${(radioValue * 1 + (tariffInit === 'Tariff L' ? 0 : 92)) * 12} ${currency.split('/')[0]}`}</td>
                    </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>


          </div>
        </div>
      </>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    tariff: state.tariffReducer,
  }
}
export default connect(mapStateToProps, { changeTariff, changeValue })(InternetCalc)
